.circle {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgb(68, 36, 36);
}
.employee-box {
  display: flex;
  padding: 10px;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 130px;
  height: 150px;
  background-color: antiquewhite;
}
.employee-box span {
  text-align: center;
}
.employee__main span {
  background-color: antiquewhite;
  /* margin-left: auto; */
  display: flex;
  /* margin-right: auto; */
  justify-content: center;
}
.employee__main {
  margin-bottom: 10px;
}
.gray {
  background-color: gray;
}
